import { useState, useRef } from 'react';
import { motion, useInView } from 'framer-motion';
import { Link } from 'react-router-dom';
import TrackCard from '../tracks/TrackCard';
import { tracks } from '../../data/tracks';

const TracksPreview = () => {
  const [expandedTrack, setExpandedTrack] = useState<number | null>(null);
  const [currentlyPlaying, setCurrentlyPlaying] = useState<number | null>(null);
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });

  // Show first 2 tracks
  const previewTracks = tracks.slice(0, 2);

  const handleTrackToggle = (trackId: number) => {
    setExpandedTrack(expandedTrack === trackId ? null : trackId);
  };

  const handleTrackPlay = (trackId: number) => {
    setCurrentlyPlaying(currentlyPlaying === trackId ? null : trackId);
  };

  return (
    <section className="section bg-ocean-sand" ref={ref}>
      <div className="container mx-auto px-4 py-16">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h2 className="text-4xl font-medium text-gray-900 mb-4 font-serif">
            
          </h2>
          <p className="text-lg text-gray-600 subtitle">
              12 
          </p>
        </motion.div>

        <div className="space-y-4 mb-12">
          {previewTracks.map((track) => (
            <TrackCard
              key={track.id}
              track={track}
              isExpanded={expandedTrack === track.id}
              onToggle={() => handleTrackToggle(track.id)}
              currentlyPlaying={currentlyPlaying === track.id}
              onPlay={() => handleTrackPlay(track.id)}
            />
          ))}
        </div>

        <div className="text-center">
          <Link
            to="/album/tracks"
            className="px-8 py-4 bg-jeju-ocean text-white rounded-full font-bold hover:bg-ocean-mist transition-all duration-300 shadow-lg hover:shadow-xl hover:-translate-y-1 inline-block"
          >
               ({tracks.length}) 
          </Link>
        </div>
      </div>
    </section>
  );
};

export default TracksPreview;
