import { useRef } from 'react';
import { motion, useInView } from 'framer-motion';
import { Link } from 'react-router-dom';
import MusicianCard from '../musicians/MusicianCard';
import { musicians } from '../../data/musicians';

const MusiciansPreview = () => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });

  // Show first 3 musicians
  const previewMusicians = [...musicians].sort((a, b) => a.id - b.id).slice(0, 3);

  return (
    <section className="section bg-white" ref={ref}>
      <div className="container mx-auto px-4 py-16">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h2 className="text-4xl font-medium text-gray-900 mb-4 font-serif">
             
          </h2>
          <p className="text-lg text-gray-600 subtitle">
            12    
          </p>
        </motion.div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 mb-12">
          {previewMusicians.map((musician, index) => (
            <MusicianCard
              key={musician.id}
              musician={musician}
              index={index}
            />
          ))}
        </div>

        <div className="text-center">
          <Link
            to="/album/musicians"
            className="px-8 py-4 bg-jeju-ocean text-white rounded-full font-bold hover:bg-ocean-mist transition-all duration-300 shadow-lg hover:shadow-xl hover:-translate-y-1 inline-block"
          >
               ({musicians.length}) 
          </Link>
        </div>
      </div>
    </section>
  );
};

export default MusiciansPreview;
